import React from "react";
import BasicCard from "./exampleCard"

function renderCard(hasCard, dark) {
    if(hasCard){
        return(
            <BasicCard dark={dark}/>
        );
    }
    return;
}

export default function Section({ title, subtitle, dark, id, hasCard }) {
  return (
    <div className={"section" + (dark ? " section-dark" : "")}>
      <div className="section-content" id={id}>
        {renderCard(hasCard, dark)}
        <h1>{title}</h1>
        <p>{subtitle}</p>
      </div>
    </div>
  );
}
