import React, {Component} from "react";
import Section from "./Section";
import dummyText from "../DummyText";
import Cover from "./Cover";
import TimelineComponent from "./Timeline";


class Structure extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      subtitles: []
    };
  }

  componentDidMount() {
    Promise.all([
      fetch("https://lucapa96.pythonanywhere.com/app/sections/cover"),
      fetch("https://lucapa96.pythonanywhere.com/app/sections/cover/subtitles")
    ])
    .then(([response1, response2]) => Promise.all([response1.json(), response2.json()]))
    .then(([data1, data2]) => {
      this.setState({
        title: data1.title,
        subtitles: data2
      });
    })
    .catch(error => console.error(error));
  }

  render() {
    return (
      <div className="App">
        <Cover
          title={this.state.title}
          subtitles={this.state.subtitles}
          id="cover"
        />
        <Section
          title="Section 2"
          subtitle={dummyText}
          dark={true}
          id="section2"
          hasCard={true}
        />
        <TimelineComponent/>
        <Section
          title="Section 3"
          subtitle={dummyText}
          dark={false}
          id="section3"
        />
        <Section
          title="Section 4"
          subtitle={dummyText}
          dark={true}
          id="section4"
        />
        <Section
          title="Section 5"
          subtitle={dummyText}
          dark={false}
          id="section5"
        />
      </div>
    );
  }
}

export default Structure;
