import React from "react";
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    coverTitle: {
      fontFamily: 'Great Vibes, cursive',
      fontSize: '10em',
      fontWeight: 'normal',
      fontStyle: 'normal',
      textAlign: 'center',
      color: '#7FB3D5',
    },
    coverSubtitle: {
      fontFamily: 'Dancing Script',
      fontSize: '2em',
      fontWeight: 'normal',
      fontStyle: 'normal',
      textAlign: 'center',
      color: '#7FB3D5',
    },
}));

export default function Cover({ title, subtitles, dark, id, hasCard }) {
  const classes = useStyles();
  const subtitlesText = [];

  for (let i = 0; i < subtitles.length; i++) {
    const subtitle = subtitles[i];
    subtitlesText.push(<p className={classes.coverSubtitle}>{subtitle}</p>);
  }

  return (
    <div className="cover">
      <div className="cover-content" id={id}>
        <h1 className={classes.coverTitle}>{title}</h1>
        {subtitlesText}
      </div>
    </div>
  );
}
